export const styleStatus = (status: string | null) => {
  if (status === 'done') {
    return { text: '', class: `text_color_success` };
  } else if (status === 'pending') {
    return { text: '', class: `text_color_default` };
  } else if (status === 'created') {
    return { text: '', class: `text_color_default` };
  } else return { text: '', class: `text_color_error` };
};
